.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct__info{
    padding-left: 20px;
}

.checkoutProduct__info > button{
    background: #f0c14b;
    border-radius: 3px;
    /* width: 100%; */
    height: 29px;
    border: 1px solid;
    /* margin-top: 10px; */
    border-color: #f0c14b;
    color: black;
    margin-top: 12px;
}

.checkoutProduct__image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct__rating{
    display: flex;
}

.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}